<template>
  <img alt="Vue logo" src="./assets/pic.jpg">
  <br>
  <button v-on:click="showData1">คริกเพื่อดูชื่อ</button>
  <button @click="showData2">คริกเพื่อดูนามสกุล</button>

  <h1>{{firstname}}{{lastname}}</h1>
  <h3>{{age}}</h3>

  <button @click="plus">เพิ่มอายุ</button>
  <button @click="reduce">ลดอายุ</button>
  <br>
  <button @click="pluss(5)">เพิ่มอายุ 5</button>
  <button>ลดอายุ 10</button>
  <form>
    <label for="">ป้อนชื่อจริง :</label>
    <input type="text" v-on:input="setfname">
    <br>
    <label for="">ป้อนนามสกุล :</label>
    <input type="text" v-on:input="setlname">
    <br>
    <label for="">ป้อนชื่อเล่น :</label>
    <input type="text" v-on:input="setnickname">
    <br>
    <label for="">ป้อนอายุ :</label>
    <input type="text" v-on:input="setage">
    <br>
    <label for="">ป้อน วัน/เดือน/ปีเกิด :</label>
    <input type="text" v-on:input="setbd">
    <br>
    <label for="">ป้อนสาขา :</label>
    <input type="text" v-on:input="setmajor">
    <br>
    <label for="">ป้อนห้อง :</label>
    <input type="text" v-on:input="setroom">
    <br>
    <label for="">ป้อนคติประจำใจ :</label>
    <input type="text" v-on:input="setmotto">
    <br>
    <label for="">ป้อนสถานที่เรียนต่อ :</label>
    <input type="text" v-on:input="setcon">
    <br>
    <label for="">ป้อนวิชาที่ไม่ชอบ :</label>
    <input type="text" v-on:input="setnotlike">
    <br>
    <!-- <button type="submit">ตกลง</button> -->
  </form>
  <h3>ชื่อ : {{fname}}</h3>
  <h3>นามสกุล : {{lname}}</h3>
  <h3>ชื่อเล่น : {{nickname}}</h3>
  <h3>อายุ : {{sage}}</h3>
  <h3>วันเกิด : {{sbd}}</h3>
  <h3>สาขา : {{smajor}}</h3>
  <h3>ห้อง : {{sroom}}</h3>
  <h3>คติประจำใจ : {{smotto}}</h3>
  <h3>สถานที่เรียนต่อ : {{scon}}</h3>
  <h3>วิชาที่ไม่ชอบ : {{snotlike}}</h3>
</template>

<script>

export default {
  name: 'App',
  data(){
    return{
      firstname : "Puminan",
      lastname : " Tananchai",
      age : 20,
      nickname : "",
      fname : "",
      lname : "",
      sage : "",
      sbd : "",
      smajor : "",
      sroom : "",
      smotto : "",
      scon : "",
      snotlike : "",
    }
  },
  methods:{
    showData1(){
      alert(this.firstname)
    },
    showData2(){
      alert(this.lastname)
    },
    plus(){
      this.age++
    },
    reduce(){
      this.age--
    },
    pluss(val){
      this.age+=val
    },


    setlname(event){
      this.lname = event.target.value
    },
    setfname(event){
      this.fname = event.target.value
    },
    setnickname(event){
      //console.log(event.target.value);
      this.nickname = event.target.value
    },
    setage(event){
      this.sage = event.target.value
    },
    setbd(event){
      this.sbd = event.target.value
    },
    setmajor(event){
      this.smajor = event.target.value
    },
    setroom(event){
      this.sroom = event.target.value
    },
    setmotto(event){
      this.smotto = event.target.value
    },
    setcon(event){
      this.scon = event.target.value
    },
    setnotlike(event){
      this.snotlike = event.target.value
    },
    
  }
};
</script>

